const text = [
  "Parts of what is now Rajasthan were partly part of the Vedic Civilisation and Indus Valley Civilization. Kalibangan, in Hanumangarh district, was a major provincial capital of the Indus Valley Civilization.[17] Another archaeological excavation at Balathal site in Udaipur district shows a settlement contemporary with the Harrapan civilisation dating back to 3000–1500 BC .Stone Age tools dating from 5,000 to 200,000 years were found in Bundi and Bhilwara districts of the state.Matsya Kingdom of the Vedic civilisation of India, is said to roughly corresponded to the former state of Jaipur in Rajasthan and included the whole of Alwar with portions of Bharatpur.[19][20] The capital of Matsya was at Viratanagar (modern Bairat), which is said to have been named after its founder king Virata.[21][need quotation to verify.Bhargava[22] identifies the two districts of Jhunjhunu and Sikar and parts of Jaipur district along with Haryana districts of Mahendragarh and Rewari as part of Vedic state of Brahmavarta. Bhargava also locates the present day Sahibi River as the Vedic Drishadwati River, which along with Saraswati River formed the borders of the Vedic state of Brahmavarta.[23] Manu and Bhrigu narrated the Manusmriti to a congregation of seers in this area only. Ashrams of Vedic seers Bhrigu and his son Chayvan Rishi, for whom Chyawanprash was formulated, were near Dhosi Hill part of which lies in Dhosi village of Jhunjhunu district of Rajasthan and part lies in Mahendragarh district of Haryana.The Western Kshatrapas (405–35 BCE), the Saka rulers of the western part of India, were successors to the Indo-Scythians and were contemporaneous with the Kushans, who ruled the northern part of the Indian subcontinent. The Indo-Scythians invaded the area of Ujjain and established the Saka era (with their calendar), marking the beginning of the long-lived Saka Western Satraps state.[25]",

  "The Gurjaras ruled for many dynasties in this part of the country, the region was known as Gurjaratra.[26] Up to the 10th century CE, almost all of North India acknowledged the supremacy of the Gurjaras, with their seat of power at Kannauj",

  "Traditionally the Brahmins, Rajputs, Gurjars, Jats, Meenas, Bhils, Dhankas, Rajpurohits, Charans, Sunaars, Yadavs, Bishnois, Meghwals, Sermals, Rajput Malis (Sainis) and other tribes made a great contribution in building the state of Rajasthan. All these tribes suffered great difficulties in protecting their culture and the land. Millions of them were killed trying to protect their land.",

  "Prithviraj Chauhan defeated the invading Muhammad Ghori in the First Battle of Tarain in 1191. In 1192 CE, Muhammad Ghori decisively defeated Prithviraj at the Second Battle of Tarain. After the defeat of Chauhan in 1192 CE, a part of Rajasthan came under Muslim rulers. The principal centers of their powers were Nagaur and Ajmer. Ranthambhore was also under their suzerainty. At the beginning of the 13th century, the most prominent and powerful state of Rajasthan was Mewar. The Rajputs resisted the Muslim incursions into India, although a number of Rajput kingdoms eventually became subservient to the Delhi Sultanate.",

  "The Rajputs put up resistance to the Islamic invasions with their warfare and chivalry for centuries. The Rana's of Mewar led other kingdoms in its resistance to outside rule. Rana Hammir Singh, defeated the Tughlaq dynasty and recovered a large portion of Rajasthan. The indomitable Rana Kumbha defeated the Sultans of Malwa, Nagaur and Gujarat and made Mewar the most powerful Rajput Kingdom in India. The ambitious Rana Sanga united the various Rajput clans and fought against the foreign powers in India. Rana Sanga defeated the Afghan Lodi Empire of Delhi and crushed the Turkic Sultanates of Malwa and Gujarat. Rana Sanga then tried to create an Indian empire but was defeated by the first Mughal Emperor Babur at Khanua. The defeat was due to betrayal by the Tomar king Silhadi of Raisen. After Rana Sangas death there was no one who could check the rapid expansion of the Mughal Empire.",

  "Hem Chandra Vikramaditya, the Hindu Emperor,[30][31] was born in the village of Machheri in Alwar District in 1501. He won 22 battles against Afghans, from Punjab to Bengal including states of Ajmer and Alwar in Rajasthan, and defeated Akbar's forces twice, first at Agra and then at Delhi in 1556 at Battle of Delhi[32] before acceding to the throne of Delhi and establishing the 'Hindu Raj' in North India, albeit for a short duration, from Purana Quila in Delhi. Hem Chandra was killed in the battlefield at Second Battle of Panipat fighting against Mughals on 5 November 1556.",

  "During Akbar's reign most of the Rajput kings accepted Mughal suzerainty, but the rulers of Mewar (Rana Udai Singh II) and Marwar (Rao Chandrasen Rathore) refused to have any form of alliance with the Mughals. To teach the Rajputs a lesson Akbar attacked Udai Singh and killed Rajput commander Jaimal of Chitor and the citizens of Mewar in large numbers. Akbar killed 20,000 – 25,000 unarmed citizens in Chittor on the grounds that they had actively helped in the resistance.",
  "Maharana Pratap took an oath to avenge the citizens of Chittor, he fought the Mughal empire till his death and liberated most of Mewar apart from Chittor itself. Maharana Pratap soon became the most celebrated warrior of Rajasthan and became famous all over India for his sporadic warfare and noble actions. According to Satish Chandra, 'Rana Pratap's defiance of the mighty Mughal empire, almost alone and unaided by the other Rajput states, constitutes a glorious saga of Rajput valor and the spirit of self-sacrifice for cherished principles. Rana Pratap's methods of sporadic warfare was later elaborated further by Malik Ambar, the Deccani general, and by Shivaji",
  "Rana Amar Singh I continued his ancestor's war against the Mughals under Jehangir, he repelled the Mughal armies at Dewar. Later an expedition was again sent under leadership of Prince Khurram, which caused much damage to life and property of Mewar. Many temples were destroyed, several villages were put on fire and women and children were captured and tortured to make Amar Singh accept surrende",
];
